.input-box {
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: 10px;
  background-color: #fff;
  background: radial-gradient(
        circle at 100% 100%,
        #ffffff 0,
        #ffffff 2px,
        transparent 2px
      )
      0% 0%/3px 3px no-repeat,
    radial-gradient(circle at 0 100%, #ffffff 0, #ffffff 2px, transparent 2px)
      100% 0%/3px 3px no-repeat,
    radial-gradient(circle at 100% 0, #ffffff 0, #ffffff 2px, transparent 2px)
      0% 100%/3px 3px no-repeat,
    radial-gradient(circle at 0 0, #ffffff 0, #ffffff 2px, transparent 2px) 100%
      100%/3px 3px no-repeat,
    linear-gradient(#ffffff, #ffffff) 50% 50% / calc(100% - 2px)
      calc(100% - 6px) no-repeat,
    linear-gradient(#ffffff, #ffffff) 50% 50% / calc(100% - 6px)
      calc(100% - 2px) no-repeat,
    linear-gradient(90deg, #f5f5f5 0%, #8257da 100%, #e789ef 100%);
  border-radius: 3px;
  padding: 3px 10px;
  box-sizing: border-box;
  backdrop-filter: blur(10px);

}

.user-icon {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

.input-textarea {
  flex-grow: 1;
  padding: 10px;
  border: none;
  border-radius: 4px;
  outline: none;
  resize: none;
  height: 40px;
  max-height: 80px;
  overflow-y: auto;
}

.input-box input[type="file"] {
  display: none;
}

.camera-icon {
  width: 24px;
  height: 24px;
  cursor: pointer;
  margin-left: 10px;
}

.input-box button {
  margin-left: 10px;
  padding: 5px 14px;
  background-color: #2e1960;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.input-box button:hover {
  background-color: #2e1960;
}

