/* InputBox.css */
.image-preview {
  position: relative;
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.preview-image {
  max-width: 40px;
  max-height: 40px;
  border-radius: 8px;
}

.remove-preview-button {
  position: absolute;
  top: -10px;
  right: 0;
  color: white;
  border: none;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 18px;
  padding: 2px !important;
}
.remove-preview-button p {
  margin-top: -5px;
}
