@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("./assets/bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  cursor: url("./assets/wans.svg") 27 20, auto;
  overflow: visible; /* Ensure that the content is scrollable */
  position: relative;
  height: 100vh; /* Set the body height to 100vh */
}

.particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden; /* Ensure particles don't create unwanted scrollbars */
  z-index: 0; /* Behind other content */
}

.particle {
  position: absolute;
  border-radius: 50%;
  background: white;
  opacity: 0.6; /* Adjust opacity to make particles slightly blurry */
  pointer-events: none;
  z-index: 1;
  animation: moveParticles linear infinite;
}

@keyframes moveParticles {
  from {
    transform: translateY(100vh); /* Start from bottom */
  }
  to {
    transform: translateY(-100%); /* Move to top */
  }
}

/* Base cursor style */
.chatbox,
.slick-slider {
  cursor: auto;
}

.container {
  max-width: 90% !important;
}

/* Magical light effect */
.magic-light {
  position: absolute;
  width: 2px; /* Size of the larger light */
  height: 2px;
  background: white;
  border-radius: 50%; /* Make the light circular */
  pointer-events: none;
  opacity: 0;
  z-index: 9999;
  animation: light-fade 1.5s ease-out forwards;
}

/* Smaller light */
.magic-light.small {
  width: 1px;
  height: 1px;
}

/* Animation for the light */
@keyframes light-fade {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-15px);
    opacity: 0;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home_text {
  color: #291853;
}
.slick-slide {
  background: linear-gradient(to right, white, transparent),
    linear-gradient(to left, white, transparent);
  background-size: 50% 100%;
  background-repeat: no-repeat;
  background-position: left, right;
}
.slick-slide img {
  width: 95% !important;
}

.slick-slide.slick-active {
  width: 295px !important;
}

.slick-list {
  position: relative;
  overflow: hidden;
}

.slick-list::before,
.slick-list::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 30px; /* Adjust width as needed */
  z-index: 1;
}

.slick-list::before {
  left: 0;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0.685),
    transparent
  );
}

.slick-list::after {
  right: 0;
  background: linear-gradient(to left, white, transparent);
}
.anim_btn {
  padding: 1px;
  border-radius: 9px;
  position: relative;
  overflow: hidden;
  width: max-content;
  float: right;
}

.top_btn {
  border-radius: 8px;
  padding: 8px 14px;
  color: white;
  background-color: #8257da; /* Purple background */
  box-shadow: rgba(16, 24, 40, 0.05) 0px 1px 2px 0px;
  border: none;
  cursor: pointer;
  position: relative;
  z-index: 1;
}
.anim_btn::before {
  content: "";
  background: conic-gradient(transparent 250deg, white 20deg, transparent 270deg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  aspect-ratio: 1 / 1;
  width: 100%;
  animation: 5s linear 0s infinite normal none running rotate !important;
  z-index: -1;
}


.anim_btn::after {
  content: "";
  background: inherit;
  border-radius: inherit;
  position: absolute;
  inset: 1px;
  height: calc(100% - 2px);
  width: calc(100% - 2px);
  z-index: -2;
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.luny_top_ai_box {
  background: #fbf6ff;
  border-radius: 10px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 6px 10px;
}

.luny_top_ai_box img {
  width: 33px;
  height: 33px;
}
.luny_top_ai_box .beta {
  border-radius: 10px;
  padding: 0px 6px 0px 8px;
}

