.chatbox {
  position: relative;
  width: 100%;
  height: 600px;
  border: 1px solid #e9e9e991;
  border-radius: 10px;
  /* padding: 10px; */
  /* padding: 60px 10px 10px 10px; */
  padding-top: 50px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}

.chat_top_bar {
  /* background: rgba(255, 255, 255, 0.2); */
  /* border-radius: 10px; */
  /* box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1); */
  /* backdrop-filter: blur(5px); */
  -webkit-backdrop-filter: blur(5px);
  /* border: 1px solid rgba(255, 255, 255, 0.3); */
  position: absolute;
  top: 0;

  border-radius: 9px 9px 0px 0px;
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(2px);
}

.chat_top_bar img {
  float: right;
}
.inbox {
  padding: 15px 10px;
  background-color: #fcfcfd;
}
.messages {
  flex-grow: 1;
  overflow-y: auto;
  padding-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  max-height: 500px;
}

.message {
  display: flex;
  align-items: flex-start;
  margin-bottom: 10px;
  text-align: left;
  animation: slide-up 0.3s ease-out;
  flex-direction: column;
  margin: 5px;
  width: 95%;
  background-color: #f2f4f7;
  padding: 10px;
  border-radius: 10px;
}
.you-message,
.luny-message {
  padding: 0;
  background: #f2f4f7;
}

.you-message,
.ai-message {
  justify-content: flex-start;
}

.sender {
  font-weight: bold;
  margin-right: 10px;
}

.text {
  background-color: #f2f4f7;
  padding: 10px;
  border-radius: 4px;
  max-width: 100%;
  font-size: 12px;
  word-wrap: break-word; /* Breaks long words at the end of the line */
  white-space: pre-wrap; /* Preserves line breaks, spaces, and tabs */
  overflow-wrap: break-word; /* Ensures text wraps within the container */
}

.text img {
  max-width: 100%;
  border-radius: 4px;
  margin-top: 10px;
}

.loading-box {
  padding: 25px;
  background-color: #fff3f1;
  border: 1px solid rgb(221, 219, 219) 3F1;
  margin-bottom: 10px;
  font-size: 12px;
  text-align: center;
  display: flex;
  align-items: center;
}

.loading-box img {
  width: 15px;
  height: 15px;
}
.proposal-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 10px;
  text-align: left;
}

.proposal {
  width: 100%;
  padding: 5px 0;
  color: #7f56d9;
  cursor: pointer;
}

.wishlist-button-container,
.wishlist-button_2,
.wishlist-button {
  /* width: 100%; */
  display: block;
  padding: 10px;
  background-color: #291853;
  color: white;
  cursor: pointer;
  text-align: center;
  border-radius: 4px;
  border: none;
  margin-top: 20px;
}

.wishlist-button_2 {
  width: 200px;
  margin: 0 auto;
}
.wishlist-button:hover {
  background-color: #291853d4;
}

@keyframes slide-up {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
